import Vue from 'vue';
import store from '../../store/index';
/**
 * 翻译字典
 * @param {*} key 值
 * @param {*} type 字典类型
 */

export function dict() {
  if (arguments.length === 1) {
    // 获取字典列表
    return store.getters['dict/getList'](arguments.length <= 0 ? undefined : arguments[0]);
  } else {
    return store.getters['dict/queryLabel']({
      type: arguments.length <= 0 ? undefined : arguments[0],
      key: arguments.length <= 1 ? undefined : arguments[1]
    });
  }
} // 挂载实例方法

Object.defineProperty(Vue.prototype, '$D', {
  value: dict
});