import axios from '@/plugins/axios';
/**
 * 获取菜单列表
 * @return {Promise}
 */

export function getMenuList() {
  return axios({
    method: 'get',
    url: '/auth/findAuthByUserId'
  }, 'simsbff');
}
/**
 * 获取按钮列表
 * @return {Promise}
 */

export function getAuthList() {
  return axios({
    method: 'GET',
    url: '/auth/getInterfaceById'
  }, 'simsbff');
}